.contain {
    width: 100%;
    border: solid black;
  
  }
  .contBtn {
    display: flex;
    flex-direction: row;
    font-size: 0.8em;
    
  }
  
  .onglets {
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 1em;
    background: #555555;
    color: #f1f1f1;
    display: flex;
    flex-wrap: wrap;
    cursor: pointer;
    border-left: solid black;
    border-bottom: solid black;
  }

  .onglets h1{
    font-size: 1.5em;
  }
  .bracelets .onglets{
    width: 25%;
  }
  .colliers .onglets{
    width: 33%;
  }
  .pendentifs .onglets{
    width: 50%;
  }

  .container {
    width: auto;
    text-align: center;
    margin: 1em;
    margin-top: 0;
  }
 
  .cont img {
    width: 100%;
  }
  
  .onglets.active {
    background: inherit;
    color: #333;
    box-shadow: 5px -4px 3px;
    border-bottom: none;
    border-left: none;
    z-index: 1000;
  }
  
  .onglets.active h1{
    font-size: 2.1em;
  }